.border-btn{
    border:1px solid #F067FF;
    color:#F067FF;
    background:none;
    border-radius:30px;
    text-transform:capitalize;
    padding:13px 48px;
    -moz-user-select:none;
    cursor:pointer;
    display:inline-block;
    font-size:16px;
    font-weight:500;
    letter-spacing:1px;
    margin-bottom:0;
    position:relative;
    transition:color 0.4s linear;
    position:relative;
    overflow:hidden;
    margin:0
}
.border-btn i{
    font-size:16px;
    margin-right:10px;
    color:#F067FF;
    -webkit-transition:.3s;
    -moz-transition:.3s;
    -o-transition:.3s;
    transition:.3s
}
.border-btn::before{
    border:1px solid transparent;
    content:"";
    position:absolute;
    left:0;
    top:0;
    width:101%;
    height:101%;
    z-index:-1;
    transition:transform 0.5s;
    transition-timing-function:ease;
    transform-origin:0 0;
    transition-timing-function:cubic-bezier(0.5, 1.6, 0.4, 0.7);
    transform:scaleY(0);
    border-radius:0
}
.border-btn:hover::before{
    transform:scaleY(1);
    border:1px solid transparent;
    color:#fff
}
